import { takeEvery, all, put } from 'redux-saga/effects';
import { sagaBoundary } from 'app/store/service';
import { COMMON__INIT_FETCH_COMMON_DATA } from './types';
import { initFetchCommonDataDoneAction } from './actions';

/**
 * @description 
 */
function * initFetchCommonData () {
  const response = {
    requiredDocumentsList: [
      { title: '', key: 'HealthDeclarationForm', visible: true },
      { title: '', key: 'InsuranceRightsConfirmation', visible: true },
      { title: '', key: 'ReviewPeriodConfirmation', visible: true },
      { title: '', key: 'FinancialDisclosure', visible: true },
      { title: '', key: 'DataUsageNotice', visible: true },
      { title: '', key: 'ApplicationInstructions', visible: true },
      { title: '', key: 'PolicyholderInfo', visible: true },
      { title: '', key: 'E-PolicyApplication', visible: true },
      { title: '', key: 'HealthQuestionnaire', visible: true },
      { title: 'CRS', key: 'CRSForm', visible: true },
      { title: '', key: 'AgentDisclosure', visible: true },
      //
      { title: '', key: 'GeneralDiseaseQuestionnaire', visible: false },
      { title: '', key: 'ThyroidDiseaseQuestionnaire', visible: false },
      { title: '', key: 'HepatitisOrHepatitisCarrierQuestionnaire', visible: false },
      { title: '', key: 'UrinarySystemDiseaseQuestionnaire', visible: false },
      { title: '', key: 'EsophagusStomachQuestionnaire', visible: false },
      { title: '//', key: 'BronchialDiseaseQuestionnaire', visible: false },
      { title: '', key: 'HypertensionQuestionnaire', visible: false },
      { title: '//', key: 'HyperuricemiaQuestionnaire', visible: false },
      { title: '', key: 'DiabetesQuestionnaire', visible: false },
      { title: '', key: 'EpilepsyQuestionnaire', visible: false },
      { title: '', key: 'HeartDiseaseQuestionnaire', visible: false },
      { title: '', key: 'TumorAndPolypQuestionnaire', visible: false },
      { title: '', key: 'PoliomyelitisQuestionnaire', visible: false },
      { title: '', key: 'TraumaAndSkeletalInjuryQuestionnaire', visible: false },
      { title: '', key: 'EyeDiseaseQuestionnaire', visible: false }
    ]
  };
  yield put(initFetchCommonDataDoneAction(response));
}

export default function * watchCommonSaga () {
  yield all([
    takeEvery(COMMON__INIT_FETCH_COMMON_DATA, sagaBoundary(initFetchCommonData))
  ]);
}
